$primaryColor: #becd00;
$primaryDarkShade: #6d7507;
.ant-btn-primary{
  color:#fff;
  background-color: #becd00;
  border-color: #becd00;

  &:hover,
  &:focus{
    color:#fff;
    background-color: #cada03;
    border-color: #cada03;
  }
}

.ant-btn:hover, 
.ant-btn:focus {
  color: #fff;
  background-color: #cada03;
  border-color: #cada03;
}

.ant-spin-nested-loading > div > .ant-spin {
  position: absolute;
  height: 100%;
  min-height:100%;
  width: 100%;
  z-index: 4;
  top: 0;
  bottom: 0;
  color:$primaryDarkShade
}

.ant-spin-dot i{
  background-color:$primaryDarkShade;
}


