// variable
$headerBG: #37c51d;
// styles 
.page-header {
    padding: 0 10px;
    height: auto;
    background-color: $headerBG;
    text-align: center;
    img {
        margin: 10px;
        border: 1px solid white;
        border-radius: 50%;
        padding: 10px;
        width: 100px;
        background: white;
    }
    h1 {
        font-size: 22px;
        padding: 10px 0;
        line-height: normal;
        margin: 0;
        /* float: left; */
        clear: both;
        display: block;
        span {
            display: block;
            font-size: 12px;
            clear: both;
        }
    }
}

.content {
    padding: 20px;
}

.breadcurmb {
    margin: 10px 0
}

.genus-species-details {
    margin-top: 20px;
    p {
        margin: 0;
        font-weight: bold;
        span {
            font-weight: normal;
        }
    }
    table {
        border: 1px solid #ddd;
        border-radius: 3px;
        font-size: 12px;
        width: 100%;
        th {
            font-weight: bold;
            border: 1px solid #ddd;
            border-bottom-width: 2px;
            padding: 5px 10px
        }
        td {
            padding: 5px 10px;
            min-height: 30px;
            border: 1px solid #ddd;
        }
    }
}


